.univer-input {
  box-sizing: border-box;
  width: 100%;
  color: rgb(var(--text-color));
}
.univer-input::selection {
  color: rgb(var(--color-white));
  background-color: rgb(var(--primary-color));
}
.univer-input-affix-wrapper {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  border: 1px solid rgb(var(--border-color));
  border-radius: var(--border-radius-base);
  transition: all 0.15s;
}
.univer-input-affix-wrapper-small {
  height: 24px;
  display: inline-flex;
  align-items: center;
  padding: 0 10px;
  font-size: var(--font-size-sm);
}
.univer-input-affix-wrapper-middle {
  height: 28px;
  display: inline-flex;
  align-items: center;
  padding: 0 10px;
  font-size: var(--font-size-sm);
}
.univer-input-affix-wrapper-large {
  height: 32px;
  display: inline-flex;
  align-items: center;
  padding: 0 10px;
  font-size: var(--font-size-sm);
}
.univer-input-affix-wrapper:hover,
.univer-input-affix-wrapper:focus-within {
  border-color: rgb(var(--primary-color));
}
.univer-input-affix-wrapper-disabled {
  cursor: not-allowed;
  border-color: rgb(var(--border-color));
}
.univer-input-affix-wrapper-disabled:hover {
  border-color: rgb(var(--border-color));
}
.univer-input-affix-wrapper input {
  padding: 0;
  border: none;
  outline: none;
  background-color: rgb(var(--bg-color-secondary));
}
.univer-input-affix-wrapper input[disabled] {
  cursor: inherit;
  color: rgb(var(--grey-200));
  background-color: rgb(var(--bg-color-secondary));
}
.univer-input-suffix {
  position: absolute;
  top: 0;
  right: 2px;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 var(--padding-sm);
  background-color: rgb(var(--bg-color-secondary));
}
.univer-input-not-allow-clear .univer-input-suffix {
  padding: 0;
}
.univer-input-clear-icon {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}
.univer-input-clear-icon-hidden {
  display: none;
}
.univer-input-slot-container {
  position: relative;
  display: flex;
}
.univer-input-slot-container > span {
  width: 100%;
}
.univer-input-slot {
  position: absolute;
  top: 0;
  right: 8px;
  display: inline-flex;
  height: 100%;
}
