.univer-tree {
  user-select: none;
  position: relative;
  color: rgb(var(--text-color));
}
.univer-tree-icon {
  position: absolute;
  top: 50%;
  left: calc(-1 * var(--padding-lg));
  transform: translateY(-50%) rotateZ(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 8px;
}
.univer-tree-icon-expand {
  transform: translateY(-50%);
}
.univer-tree-list {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 100%;
}
.univer-tree-list .univer-tree-list {
  overflow: hidden;
  height: 0;
}
.univer-tree-list .univer-tree-list-expand {
  height: inherit;
}
.univer-tree-list-item {
  position: relative;
  padding-left: var(--padding-xl);
  font-size: 13px;
}
.univer-tree-list-item > a {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.univer-tree-list-item-title {
  display: inline-block;
  height: 100%;
  margin: 0 4px;
  flex-shrink: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.univer-tree-list-item-content {
  margin: 4px 0;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
.univer-tree-list-item-content-selected-icon {
  position: absolute;
  top: 50%;
  left: calc(-1 * var(--padding-xl));
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-lg);
}
