.univer-editor-container {
  position: absolute;
  z-index: 10;
  top: 200px;
  left: 100px;
  display: flex;
  width: 100px;
  height: 50px;
  border: 1px solid rgb(var(--primary-color));
  box-shadow: none;
  box-sizing: content-box;
}
.univer-editor-container .univer-editor-input {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
}
.univer-editor-container .univer-editor-input canvas {
  position: absolute;
}
.univer-editor-container .univer-editor-input .univer-sheet-embedding-formula-editor-wrap {
  height: auto;
  border: none;
  padding: 0;
  border-radius: 0;
}
